export default {
    name: 'Categories',
    data: () => ({
        search: '',
        categoryPosts: null
    }),
    props: {
        scrolled: Number,
        bgHeight: Number
    },
    computed: {
        categories: function() {
            return this.$store.state.categories.categories;
        }
    },
    async fetch({ store }) {
        await store.dispatch('categories/fetchCategories');
    }
}